@import './Variables.scss';

body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background: $softwhite;
	font-family: 'Open Sans', sans-serif;
    -webkit-tap-highlight-color: transparent;
}
.wrapper {
    padding: 80px 15px 15px 15px;    
            display: grid;
}
.container{
    max-width: 1000px;
    margin: 0 auto;
    width: 100%
}

.wrapper{
    padding-left: 10px;
    padding-right: 10px;
}
@media (min-width: 800px){
    .categorys_products{
        display: inline-flex;
        flex-wrap: wrap;
        width: 100%
    }
    .wrapper{
        padding-left: 15px;
        padding-right: 15px;
    }
}

@media (min-width: 1300px){
    .container{
        max-width: 1100px;
    }

}