@import './Variables.scss';
.checkout-wrapper{
    margin-top: 15px;
}
.checkout-container{
    background-color: $white;
    padding: 15px;
    border-radius: $radius;
    margin: 0 auto;
    text-align: center;
    position: relative;
}
.checkout-title{
    margin: 10px 0 20px;
        font-size: 22px;
        text-align: center;
        color: $heavy_green;
        font-weight: bold;
}
.checkout-container label {
	font-family: 'Open Sans', sans-serif;
    color: $purple !important;
    font-weight:500 !important;
}
.Mui-focused .MuiOutlinedInput-notchedOutline {
    border-color: $purple !important;
    border-width: 1px !important;
}
.checkout-button-container{
    display: flex;
    align-items: flex-end;
    justify-content: space-evenly; 
}
.back-to-products{
    display: inline-block;
}
.MuiInputLabel-root {
    font-size: .9rem !important;
    line-height: .9rem !important;
}
.MuiSelect-select, .MuiOutlinedInput-input{
    padding: 10px !important;
}
.order-brief-container {
    display: inline-flex;
    width: calc(100% - 30px);
    flex-wrap: wrap;
    background-color: $lightgreen;
    border-radius: $radius;
    padding: 15px;
    justify-content: center;
}
.checkout-item{
    width: 100%;
    display: inline-flex;
    padding-bottom: 5px;
    justify-content: space-between;
    /* max-width: 500px; */
}
.order-brief-wrapper {
    display: inline-flex;
    width: 100%;
    flex-wrap: wrap;
    background-color: $lightgreen;
    border-radius: $radius;
    justify-content: center;
    margin-bottom: -15px;
}

.order-brief-container {
    padding-bottom: 15px;
    max-width: 590px;
}
.last-total{
    border-top: 1px solid $green;
    margin-top: 10px;
    padding-top: 10px;
}
.order-brief-title{
    width: 100%;
    padding: 0;
    margin: 0 auto;
    text-align: left;
    font-size: 18px;
    font-weight: 700;
    color: $heavy_green;
    font-weight: bold;
    cursor: pointer;
    color: $white;
    position: relative;
    padding: 10px;
    background-color: $green;
    border-radius: 10px;
    cursor: pointer;
}

.order-brief-title:after{
    content: "";
    position: absolute;
    top: 8px;
    right: 15px;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 7px solid $white;
    transform: rotate(180deg);
    margin-top: 10px;
    transition: 0.35s;
}
.closed-brief .order-brief-title:after{
    transform: rotate(0deg);
    margin-top: 10px;
}

.order-brief-wrapper.closed-brief{
    background-color: $green  
}

.order-brief-wrapper.closed-brief .order-brief-title{
    margin-bottom: 15px;
}

.order-brief-wrapper.closed-brief .order-brief-container {
    display: none;
}
@media (max-width: $mobile){
    .checkout-container .MuiButton-root{
        width: 100% !important;
    }
    .checkout-button-container{
        width: 100%;
        flex-wrap: wrap;
    }
    .checkout-button-container button{
        width: 100% !important;
        display: block
    }
}
.MuiSelect-select{
    text-align: left;
}
.MuiInputLabel-root{
    z-index: 0 !important;
}

.error-msg {
    text-align: left;
    padding-left: 15px;
    color: $heavy_green;
    background-color: $erroryellow;
    padding: 5px 2px 5px 14px;
    font-weight: 600;
    font-size: 12px;
    border-radius: 4px;
    margin-top: 2px;
}

.checkout-container.hidden{
    display: none;
}